.content {
  background-color: #121212;
}

body {
  background-color: #121212;
}

.backarrow {
  width: 100%;
  color: white;
  font-size: 40px;
  position: relative;
  z-index: 1;
}

.backarrow a {
  display: block;
  padding: 16px;
}

.Westnavbar {
  color: white;
  font-size: 30px;
  padding-left: 93%;
  margin-right: 100px;
  text-decoration: none;
  position: fixed;
  top: 0;
  z-index: 1;
  margin-top: 8%;
}

.Westnavbar a {
  display: block;
  padding: 16px;
}

.Eventsheading {
  font-family: arpona, serif;
  font-style: bold;
  font-weight: 500;
  font-size: 20px;
  color: white;
  padding-left: 115px;
}

.hr {
  color: white;
  border: 0;
  width: 85%;
  padding-left: 115px;
  height: 5px;
}

.West-date {
  display: flex;
  flex-direction: row;
}

.West-Date1 {
  font-weight: 600;
  padding-right: 5px;
}

.West-location {
  display: flex;
  flex-direction: row;
}

.West-location1 {
  font-weight: 600;
  padding-right: 5px;
}

.West-role {
  display: flex;
  flex-direction: row;
}

.West-role1 {
  font-weight: 600;
  padding-right: 5px;
}

.West-heading {
  display: flex;
  flex-direction: row;
  position: relative;
  padding-top: 50px;
}

.West-heading > p {
  font-family: "ArchivoBlack-Regular";
  font-size: 100px;
  color: white;
  padding-left: 115px;
  bottom: -15px;
  text-align: left;
  position: absolute;
}

.West-heading > img {
  width: 55%;
  height: auto;
  padding: 0px 150px 110px 540px;
  float: right;
  vertical-align: top;
  max-width: 100%;
}

.West-topimage {
  opacity: 0.64;
}

.West-information {
  color: white;
  font-family: arpona, serif;
  font-style: normal;
  font-weight: 100;
  font-size: 20px;
  padding-left: 115px;
  line-height: 40px;
  margin-top: -60px;
}

.West-firsttier > h1 {
  color: white;
  font-size: 40px;
  font-family: arpona, serif;
  font-style: bold;
  font-weight: 750;
  line-height: 50px;
  letter-spacing: -1px;
}

.West-firsttiersubheading {
  color: white;
  font-family: arpona, serif;
  font-style: normal;
  font-weight: 100;
}

.West-secondtier {
  display: flex;
  flex-direction: row;
}

.West-secondtierimg {
  width: 30%;
  height: auto;
  padding-left: 150px;
  padding-top: 250px;
  max-width: 100%;
}

.West-thirdtierimg {
  width: 450px;
  height: 300px;
  padding-left: 250px;
  padding-top: 50px;
  max-width: 100%;
}

.West-secondheading {
  color: white;
  font-size: 40px;
  font-family: arpona, serif;
  font-style: bold;
  font-weight: 750;
  line-height: 50px;
  letter-spacing: 3px;
}

.West-secondsubheading {
  color: white;
  font-family: arpona, serif;
  font-style: normal;
  font-weight: 100;
  padding-left: 48%;
  padding-top: 20px;
  padding-right: 180px;
}

.West-fourthimage > img {
  width: 420px;
  height: 300px;
  padding-top: 80px;
  padding-left: 300px;
}

.West-bottom {
  color: white;
  font-family: "ArchivoBlack-Regular";
  font-size: 40px;
  display: flex;
  flex-direction: row;
  padding-top: 150px;
  margin-bottom: 50px;
}

.West-bottom > .West-Home {
  padding-left: 25%;
}

.West-bottom > .West-NextEvent {
  padding-left: 25%;
}

.WestBFA {
  color: white;
  font-family: futura-pt, sans-serif;
  font-style: bold;
  font-weight: 800;
  font-size: 12px;
  text-align: center;
  margin-bottom: 50px;
}

@viewport {
  width: device-width;
  zoom: 1;
}

@media only screen and (max-width: 600px) {
  .Eventsheading {
    padding-left: 25px;
  }

  .hr {
    padding-left: 25px;
    width: 90%;
  }

  .Westnavbar {
    position: fixed;
    font-size: 25px;
    top: 0;
    z-index: 1;
    margin-top: 120px;
    margin-left: -5%;
  }

  .West-heading > p {
    font-size: 45px;
    margin-left: -90px;
    margin-bottom: -35%;
  }

  .West-heading {
    flex-direction: column-reverse;
  }

  .West-heading > img {
    width: 85%;
    height: auto;
    margin-left: -510px;
    margin-top: -25px;
  }

  .West-information {
    font-size: 15px;
    padding-top: 50%;
    padding-left: 25px;
    line-height: 25px;
  }

  .West-firsttier > h1 {
    font-size: 25px;
    letter-spacing: normal;
  }

  .West-secondtier {
    flex-direction: column-reverse;
  }

  .West-secondtierimg {
    width: 85%;
    height: auto;
    display: block;
    padding-left: 5%;
    padding-top: 100px;
  }

  .West-thirdtierimg {
    width: 85%;
    height: auto;
    display: block;
    padding-left: 5%;
    padding-top: 100px;
  }

  .West-secondheading {
    font-size: 25px;
    line-height: 35px;
    letter-spacing: normal;
  }

  .West-secondsubheading {
    padding-left: 25px;
    padding-right: 25px;
  }

  .West-fourthimage > img {
    width: 85%;
    height: auto;
    display: block;
    padding-left: 5%;
    padding-top: 100px;
  }

  .West-bottom {
    font-size: 25px;
  }

  .West-bottom > .West-Home {
    padding-left: 50px;
  }

  .West-bottom > .West-NextEvent {
    padding-left: 25%;
    margin-top: -15px;
    margin-bottom: 20px;
  }

  .WestBFA {
    color: white;
    font-family: futura-pt, sans-serif;
    font-style: bold;
    font-weight: 800;
    font-size: 10px;
    text-align: center;
    margin-bottom: 50px;
  }
}
